.backdrop_pop-up {
  z-index: 100;
  width: 100%;
  height: 100%;
  opacity: 1;
  transition-property: opacity;
  transition-duration: .25s ease-in-out;
  background-color: #0000004d;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

@media screen and (max-width: 767px) {
