    padding: 0 20px;
  }
}

.backdrop_pop-up--is-hidden {
  opacity: 0;
  pointer-events: none;
}

.backdrop_pop-up--is-hidden .modal_pop-up {
  transform: translate(-50%, -50%)scale(.7);
}

@media screen and (max-width: 335px) {
  .backdrop_pop-up--is-hidden .modal_pop-up {
    transform: translate(0)scale(.7);
  }
}

.modal_pop-up {
  width: 100%;
  background-color: #fff;
  border: 2px solid #111;
  border-radius: 18px;
  margin-top: 50px;
  margin-bottom: 50px;
  padding: 40px 24px;
