.switch-box {
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

#toggle {
  opacity: 0;
  touch-action: none;
}

#toggle:checked ~ .slider-theme {
  background: linear-gradient(#4f2ee8 0%, #686868 100%);
}

#toggle:checked ~ .slider-theme:before {
  background-color: #fff;
  transform: translate(19px);
  box-shadow: inset 1px 1px 2px #0000001a;
}

#toggle:checked ~ .slide-block {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  left: 0;
}

.slider-theme {
  width: 40px;
  height: 20px;
  cursor: pointer;
  background: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
  border-radius: 30px;
  box-shadow: inset 1px 1px 2px #0000001a;
}

.slider-theme:before {
  width: 18px;
  height: 18px;
  content: "";
  background-color: #fff;
  border-radius: 50%;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  bottom: 1px;
  left: 15px;
}

