.light-theme {
  background-color: $light-theme-main-background-color;
  color: $black-color;
}

body.dark-theme {
  background-color: $dark-theme-main-background-color;
  color: $white-color;
  fill: $white-color;
}

.dark-theme_header {
  background-color: $black-color;
}

.dark-theme_header-hover {
  color: $white-color;
  fill: $white-color;
  &:hover,
  &:focus {
    color: $black-color;
    fill: $black-color;
  }
}

.dark-theme-cuttent-page {
  color: $black-color;
}
.dark-theme-category-list_item {
  color: rgba(255, 255, 255, 0.6);
}
